<template>
      <div class="selcet-category">
        <div class="selcet-category__title">Select a category</div>
        <ul class="selcet-category__category-list">
            <li class="selcet-category__list-item" 
            v-for="cat in categoryList" 
            :key="cat" 
            @click="select(cat)" 
            :class="{'selcet-category__list-item_active':selectItem==cat}">
                {{cat}}
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props:['categoryList'],
    data(){
        return {
            selectItem:''
        }
    },
    methods:{
        select(cat){
            this.selectItem = cat
            this.$emit('select', cat)
        }
    },
}
</script>

