<template>
  <ul class="selected-options">
<li class="selected-options__item">
<div 
class="selected-options__name"
v-for="item in itemList"
:key="item">
  {{item}}
  <button class="selected-options__cancel-btn" @click="cancel(item)">
    <img src="/img/icons/Union.svg" alt="">
  </button>
</div> 
</li>
</ul>
</template>

<script>
export default {
  props:['itemList'],
  methods:{
    cancel(item){
      this.$emit('cancel', item)
    }
  },
}
</script>

