<template>
  <main class="main">
    <AppSteps :step="1"/>
    <div class="add-product">
        <h2 class="add-product__title">Add a product</h2>
       <AppCustomSelect 
       v-if="!selectedGame"
       class="add-product__game-select"
       :optionList="gamesList"
       :type="'search'"
       :placeholder="'Enter the game'"
       @select="selectGame"/>
    </div>
    <div class="selected-items" v-if="selectedGame">
        <div class="selected-items__title">
            Game
        </div>
        <AppSelectedItems :itemList="[selectedGame]" @cancel="clearGame"/>
    </div>
    <AppSelectCategory :categoryList="categoryList" v-if="selectedGame" @select="selectCat"/>
    <ul class="main__step-btns" v-if="selectedCategory">
        <li><button class="step-next-btn" @click="$router.push('/step2')">next</button></li>
    </ul>
</main>
</template>

<script>

import AppSteps from '@/components/AppSteps.vue';
import AppCustomSelect from '@/components/AppCustomSelect.vue'
import AppSelectCategory from '@/components/AppSelectCategory.vue';
import AppSelectedItems from '@/components/AppSelectedItems.vue';

export default {
    name: "HomeView",
    components: {
        AppSteps,
        AppCustomSelect,
        AppSelectCategory,
        AppSelectedItems
    },
    data(){
      return {
        gamesList: ['World of Warcraft', 'World of Tanks', 'World of Warships'],
        categoryList:[ 'Boosting', 'Coaching', 'Gold', 'Accounts', 'Other'],
        selectedGame:'',
        selectedCategory:''
      }
    },
    methods:{
      selectGame(game){
        this.selectedGame=game
      },
      clearGame(){
        this.selectedGame = ''
      },
      selectCat(cat){
        this.selectedCategory = cat
      }
    },
}
</script>
