<template>
   <div class="add-product-select">
            <div class="add-product-select__input-container">
                <input 
                type="text" 
                class="add-product-select__input" 
                :placeholder="placeholder" 
                v-model="searchString"
                @focus="search"
                @blur="isOpen=false">
                <button :class="[{'add-product-select__icon-search':type=='search'}, {'add-product-select__icon-arrow':type!=='search'}]"></button>
            </div>
            <div 
            class="add-product-select__otions-list"
            :class="{'add-product-select__otions-list_hidden':!isOpen}">
                <div 
                v-for="option in searchOptions" 
                class="add-product-select__option" 
                :data-value="option" 
                @mousedown="select(option)"
                :key="option">
                    {{option}}
                </div>
            </div>
        </div>
</template>

<script>
import { placeholder } from '@babel/types'

export default {
    props:['optionList','type', 'placeholder'],
    data(){
        return {
            searchString:'',
            isOpen: false,
        }
    },
    computed:{
        searchOptions(){
            return this.optionList.filter(item=>{
                if (this.searchString)
                return item.toLowerCase().includes(this.searchString.toLowerCase())
            })
        }
    },
    methods:{
        search(){
            this.isOpen = true
        },
        select(option){
            console.log(option);
            this.$emit('select', option)
        }
    },
}
</script>

